.container {
    margin-top: 20px;
    max-width: 1370px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15%;
    padding-right: 15%;
}

.products_100 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 20px;
}

.product_blok {
    max-width: 230px;
    max-height: 450px;
    padding: 10px;
    overflow: hidden;
    margin: 5px;

    background: rgba(255, 255, 255, 0.22);
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
}

.product_blok:hover {
    background: rgba(255, 255, 255, 0.22);
    box-shadow: 4px 4px 26px 1px rgba(0, 0, 0, 0.25);
    border-radius: 15px;
}

.product_garantia {
    width: 192px;
    height: 42px;
    background-color: #28AC00;
    border-radius: 15px;
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    color: white;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
}

.product_gar {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.product_photo {
    width: 100%;
}

.products_name {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 27px;
    display: flex;
    align-items: center;

    color: #000000;
}

.products_numb {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    display: flex;
    align-items: center;

    color: #28AC00;
}

.products_price {
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    color: #000000;
}

.products_button {
    color: #28AC00;
    width: 70%;
    background: #FFFFFF;
    border: 2px solid #28AC00;
    border-radius: 15px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 37px;
    text-align: center;
}

.btn_v_korz {
    display: flex;
    justify-content: space-between;
}

.products_button_icon {
    font-size: 25px;
    color: #28AC00;
    width: 25%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background: #FFFEFE;
    border: 2px solid #28AC00;
    border-radius: 15px;

}

@media (max-width: 1440px) {
    .product_blok {
        max-width: 200px;
    }
}

@media (max-width: 768px) {
    .container {
        padding-left: 3%;
        padding-right: 3%;
    }

    .product_blok {
        max-width: 200px;
    }
}

@media (max-width: 426px) {
    .product_blok {
        max-width: 150px;
    }

    .products_button {
        font-size: 13px;
    }

    .products_button_icon {
        font-size: 18px;
    }

    .product_garantia {
        height: 35px;
        font-size: 13px;
    }

    .products_name {
        font-size: 14px;
        line-height: 20px;
    }

    .products_numb {
        font-size: 13px;
        line-height: 20px;
    }
}

@media (max-width: 377px) {
    .product_blok {
        max-width: 120px;
    }
}