.header_service {
    font-family: 'Poppins', sans-serif;
}

.container {
    margin-top: 20px;
    max-width: 1370px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15%;
    padding-right: 15%;
}

.service_photo {
    width: 100%;
    object-fit: cover;
    height: 300px;
}


.services_h2 {
    top: -200px;
    position: relative;
    text-align: center;
    color: white;
    font-size: 58px;
}

.name_service {
    position: relative;
    top: -50px;
    font-size: 16px;
    font-weight: 600;
    color: #000000;
    line-height: 1.5;
    text-align: center;
    text-transform: uppercase;
}

.text_service {
    font-size: 14px;
    font-weight: 400;
    color: #515151;
    line-height: 1.35;
    font-family: inherit;
    margin: 0 0 10px;
}

ul {
    padding-left: 40px;
    line-height: 2;
}

.reg_service {
    font-size: 20px;
    font-weight: 400;
    text-align: center;
    padding-bottom: 20px;
}

.form_control {
    height: 40px;
    border: 1px solid #707070;
    border-radius: 3px;
    font-size: 14px;
    font-weight: 400;
    color: #515151;
    line-height: 1.35;
    font-family: inherit;
    padding: 10px 15px;
    margin-bottom: 15px;
    background-color: #fff;
}

.widht1 {
    width: 90%;
}

.reg_container {
    max-width: 710px;
    margin: 0 auto;
}

.s_button {
    width: 200px;
}

@media (max-width: 1440px) {}

@media (max-width: 768px) {
    .container {
        padding-left: 5%;
        padding-right: 5%;
    }

    .services_h2 {
        top: -180px;
        font-size: 60px;
    }

    .reg_container {
        max-width: 610px;
    }
}

@media (max-width: 426px) {
    .service_photo {
        height: 180px;
    }

    .services_h2 {
        top: -160px;
    }

    .reg_container {
        max-width: 280px;
        margin: 0 auto;
    }
}

@media (max-width: 377px) {
    .service_photo {
        height: 130px;
    }

    .services_h2 {
        top: -100px;
        font-size: 40px;
    }

    .reg_container {
        max-width: 280px;
        margin: 0 auto;
    }
}