.container {
    margin-top: 20px;
    max-width: 1370px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15%;
    padding-right: 15%;
}

.mobi_menu {
    width: 100%;
    height: 40px;
    background-color: #141414;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    color: rgb(255, 255, 255);
    font-weight: 600;
    font-size: 15px;
}

.mobi_items {
    display: flex;
    align-items: center;
    padding: 0 20px;
}

.logo_site_2 {
    width: 30px;
}

.mobi_m {
    display: none;
}


@media (max-width: 768px) {
    .container {
        padding-left: 3%;
        padding-right: 3%;
    }

    .mobi_items {
        padding: 5px 20px;
        display: none;

    }

    .mobi_m {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 2px 0;
        /* border-bottom: 2px solid white; */
        width: 100%;
    }

    .mobi_items_m {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .mobi_icon_m {
        width: 30px;
        height: 30px;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }

    .mobi_menu {
        height: 100%;
        flex-direction: column;
        align-items: center;
    }

    .mobi_katalog_item {
        display: flex;
        padding: 5px 0;
    }

    .mobi_katalog {
        display: none;

    }

    .mobi_katalog.active {
        display: inherit;
    }



}