.header {
    width: 100%;
    height: 50px;
    background-color: #141414;
    color: white;
    position: relative;
}

.contents {
    max-width: 1370px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15%;
    padding-right: 15%;
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: space-between;
}

.logo {
    display: flex;
    width: auto;
    align-items: center;
    cursor: pointer;
}

.logo_site {
    width: 50px;
}

.name_site {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 40px;
    padding-left: 5px;
    text-decoration: none;
    color: white;
}

.menu {
    display: flex;
    font-weight: 600;
    font-size: 15px;
    width: 370px;
    justify-content: space-evenly;
}

.menu_content {
    cursor: pointer;
    text-decoration: none;
    color: white;
}

.icon_menu {
    display: flex;
    align-items: center;
    font-size: 25px;

}

.icon_m {
    width: 70px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

.icon_kach.active {
    color: #28AC00;
}

.shop-cart {
    position: absolute;
    top: 60px;
    width: 400px;
    min-height: 200px;
    right: 50px;
    background-color: #ffffff;
    border-radius: 10px;
    z-index: 100;
    -webkit-box-shadow: 0px 0px 50px 2px rgba(20, 20, 20, 0.85);
    box-shadow: 0px 0px 50px 2px rgba(20, 20, 20, 0.85);
}

@media (max-width: 768px) {
    .contents {
        padding-left: 3%;
        padding-right: 3%;
    }

    .menu {
        display: none;
    }
}